import { ModuleWithProviders } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

export const appRoutes: Routes = [
  { path: '', redirectTo: '/home', pathMatch: 'full' },
  { path: 'home', loadChildren: () => new Promise(function (resolve) {  (require as any).ensure([], function (require: any) {    resolve(require('./home/home.module')['HomeModule']);  });}) },
  { path: 'data-list', loadChildren: () => new Promise(function (resolve) {  (require as any).ensure([], function (require: any) {    resolve(require('./data-list/data-list.module')['DataListPageModule']);  });}) },
  { path: 'buttons', loadChildren: () => new Promise(function (resolve) {  (require as any).ensure([], function (require: any) {    resolve(require('./buttons/buttons.module')['ButtonsPageModule']);  });}) },
  { path: 'forms', loadChildren: () => new Promise(function (resolve) {  (require as any).ensure([], function (require: any) {    resolve(require('./forms/forms.module')['FormsPageModule']);  });}) },
  { path: 'cards', loadChildren: () => new Promise(function (resolve) {  (require as any).ensure([], function (require: any) {    resolve(require('./cards/cards.module')['CardsPageModule']);  });}) },
  { path: 'table', loadChildren: () => new Promise(function (resolve) {  (require as any).ensure([], function (require: any) {    resolve(require('./table/table.module')['TablePageModule']);  });}) },
  { path: 'misc', loadChildren: () => new Promise(function (resolve) {  (require as any).ensure([], function (require: any) {    resolve(require('./miscellaneous/miscellaneous.module')['MiscPageModule']);  });}) },
  { path: 'colors', loadChildren: () => new Promise(function (resolve) {  (require as any).ensure([], function (require: any) {    resolve(require('./colors/colors.module')['ColorsPageModule']);  });}) },
  { path: 'typefaces', loadChildren: () => new Promise(function (resolve) {  (require as any).ensure([], function (require: any) {    resolve(require('./typefaces/typefaces.module')['TypefacesPageModule']);  });}) }
];

export const appRouting: ModuleWithProviders = RouterModule.forRoot(appRoutes);



